<template>
  <section class="main">
    <ul class="todo-list">
      <li class="todo" v-for="(item, index) in body_list" :key="item.id">
        <div class="view">
          <span class="index">{{ index + 1 }}.</span>
          <label
            :class="{ isCompleted: item.completed }"
            :style="{ color: item.color }"
            >{{ item.task }}</label
          >
          <div
            class="editColor"
            @click="changeColor(item.color, item.id)"
            :style="{ backgroundColor: item.color }"></div>
          <input
            id="checkbox"
            type="checkbox"
            @change="markComplete($event, item.id)"
            :checked="item.completed" />
          <button class="destroy" @click="del(item.id)"></button>
        </div>
      </li>
    </ul>
  </section>
</template>

<script>
// #4D4D4D
export default {
  props: ["body_list"],
  methods: {
    del(id) {
      this.$emit("delTask", id);
    },

    markComplete(e, id) {
      this.$emit("changeComplete", {
        id,
        completed: e.target.checked,
      });
    },
    changeColor(color, id) {
      console.log(color);
      const colors = ["#4D4D4D", "red", "blue", "pink", "green", "#9b9b0b"];
      color = colors[(colors.indexOf(color) + 1) % colors.length];
      this.$emit("changeColor", { color, id });
    },
  },
};
</script>

