<template>
  <!-- 主体区域 -->
  <div>
    <section id="app">
      <!-- 输入框 -->
      <XhHeader @addTask="addFn"></XhHeader>

      <!-- 列表区域 -->
      <XhBody
        :body_list="list"
        @delTask="delFn"
        @changeComplete="changeComplete"
        @changeColor="changeColor"></XhBody>

      <!-- 统计和清空 -->
      <XhFooter :len="list.length" @delAllTask="delAll"></XhFooter>
    </section>
    <h4>
      tips: 点击圆形色圈可以改变事项的颜色，勾选矩形框可以标记事项已完成哦 ^_^
    </h4>
  </div>
</template>

<script>
import XhHeader from "./components/XhHeader.vue";
import XhBody from "./components/XhBody.vue";
import XhFooter from "./components/XhFooter.vue";
export default {
  data() {
    return {
      list: [
        {
          id: 1,
          task: "111",
          completed: false,
          color: "red",
        },
        {
          id: 2,
          task: "222",
          completed: false,
          color: "blue",
        },
        {
          id: 3,
          task: "333",
          completed: true,
          color: "#4D4D4D",
        },
      ],
    };
  },
  components: {
    XhHeader: XhHeader,
    XhBody: XhBody,
    XhFooter: XhFooter,
  },
  methods: {
    addFn(task) {
      this.list.push({
        id: this.list.length + 1,
        task: task,
        completed: false,
        color: "#4D4D4D",
      });
    },
    delFn(id) {
      this.list = this.list.filter((item) => item.id !== id);
    },
    delAll() {
      this.list = [];
    },
    changeComplete(data) {
      this.list.forEach((item) => {
        if (item.id === data.id) {
          item.completed = data.completed;
        }
      });
    },
    changeColor(data) {
      console.log(data);
      this.list.forEach((item) => {
        if (item.id === data.id) {
          item.color = data.color;
        }
      });
    },
  },
  watch: {
    list: {
      handler(newVal) {
        localStorage.setItem("task", JSON.stringify(newVal));
      },
      deep: true,
    },
  },
  created() {
    this.list = JSON.parse(localStorage.getItem("task")) || this.list;
  },
};
</script>

<style></style>
