<template>
  <header class="header">
    <h1>待办清单</h1>
    <input
      v-model="task"
      placeholder="请输入待办事项"
      class="new-todo"
      @keydown.enter="add" />
    <button class="add" @click="add">添加</button>
  </header>
</template>

<script>
export default {
  data() {
    return {
      task: "",
    };
  },
  methods: {
    add() {
      this.$emit("addTask", this.task);
      this.task = "";
    },
  },
  props: {},
};
</script>

<style></style>
